<template>
    <div class="app-container">
        <el-table :data="tableData" stripe style="width:100%">
            <el-table-column type="expand">
                <template slot-scope="props">
                    <el-form label-position="left" inline class="admin-table-expand">
                        <el-form-item label="用户姓名">
                            <span>{{ props.row.real_name }}</span>
                        </el-form-item>
                        <el-form-item label="创建时间">
                            <span>{{ props.row.create_time }}</span>
                        </el-form-item>
                        <el-form-item label="用户 ID">
                            <span>{{ props.row.id }}</span>
                        </el-form-item>
                        <el-form-item label="手机号码">
                            <span>{{ props.row.phone_number }}</span>
                        </el-form-item>
                        <el-form-item label="邮箱地址">
                            <span>{{ props.row.mail_address }}</span>
                        </el-form-item>
                    </el-form>
                </template>
            </el-table-column>
            <el-table-column
                label="用户ID"
                prop="id">
            </el-table-column>
            <el-table-column
                label="用户名"
                prop="name">
            </el-table-column>
            <el-table-column
                label="最后登录"
                prop="last_time">
            </el-table-column>
            <el-table-column
                label="描述"
                prop="desc">
            </el-table-column>
            <el-table-column
                label="操作">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    type="primary"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>

        </el-table>
    </div>
</template>
<script>
import { allUsersInfo } from '@/api/user';
import { dateFtt } from '@/utils/index';

export default {
  created() {
    this.getAllUsersInfo();
  },
  data() {
    return {
      tableData: []
    };
  },
  methods: {
    // 获取管理员信息
    getAllUsersInfo() {
      allUsersInfo()
        .then(response => {
          this.tableData = response.Data;
          this.tableData.forEach(element => {
            element['create_time'] = dateFtt(
              'yyyy-MM-dd hh:mm:ss',
              new Date(element['create_time'])
            );
            element['last_time'] = dateFtt(
              'yyyy-MM-dd hh:mm:ss',
              new Date(element['last_time'])
            );
          });
          console.log(response.Data);
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 编辑
    handleEdit(index, row) {
      this.$router.push({ path: 'adminedit', query: { username: row.name }});
      // this.$router.push({path: '/', query: {参数名: '参数值'})

      // this.$router.push({name: '/', params: {参数名: '参数值'})
    },
    // 删除
    handleDelete(index, row) {}
  }
};
</script>
<style>
.admin-table-expand {
  font-size: 0;
}
.admin-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.admin-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>



